.grid-container {
    display: grid;
    grid-template-rows: repeat(8, 1fr);
    grid-template-columns: repeat(8, 1fr);
    grid-gap: 1em; 
}

.item {
    background-color: lightblue;
    padding: 20px;
    text-align: center;
    border: 1px solid #000;
}


.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #333;
    color: white;

    grid-row: 1 / 2;
    grid-column: 1 / 9;
}

.logo {
    font-size: 1.5rem;
    font-weight: bold;
}

.navbar a {
    color: white;
    text-decoration: none;
    margin-left: 15px;
}
  
.navbar a:hover {
    text-decoration: underline;
}
  










  
.item-1 {
    grid-row: 2 / 4;
    grid-column: 1 / 5;
}

.item-2 {
    grid-row: span 2;
    grid-column: span 2;
}
  